// @flow
import React, { Component } from 'react';
import { connect } from 'react-redux';
import Grid from '@material-ui/core/Grid';
import Button from '@material-ui/core/Button';
import IconButton from '@material-ui/core/IconButton';
import MenuIcon from '@material-ui/icons/Menu';
import MenuItem from '@material-ui/core/MenuItem';
import Menu from '@material-ui/core/Menu';

import logo from '../../assets/atx_fabrications.png';

import './index.css';

type Props = {}

type State = {
  anchorEl: any,
  headerClass: string,
}

export class Header extends Component<Props, State> {
  constructor(props: Props) {
    super(props);

    this.state = {
      anchorEl: null,
      headerClass: 'transparent',
    };
  }

  componentDidMount() {
    window.addEventListener('scroll', this.scrollFunction, true);
  }

  scrollFunction = () => {
    if (document.body.scrollTop > 200 || document.documentElement.scrollTop > 200) {
      this.setState({ headerClass: "white"});
    } else {
      this.setState({ headerClass: "transparent"});
    }
  };

  handleMenu = event => {
    console.log('what is anchorEl', event.currentTarget);
    this.setState({ anchorEl: event.currentTarget });
  };

  handleClose = () => {
    this.setState({ anchorEl: null });
  };

  render() {
    const { anchorEl, headerClass } = this.state;
    const open = Boolean(anchorEl);

    return (
      <div className="header">
        <header className={headerClass}>
          <Grid container direction="row" xs={12} alignItems="center">
            <Grid item align="start" xs={4}>
              <IconButton
                color="black"
                aria-label="Menu"
                className="menu"
                aria-owns={open ? 'menu-appbar' : null}
                aria-haspopup="true"
                onClick={this.handleMenu}
              >
                <MenuIcon />
              </IconButton>
              <Menu
                anchorEl={anchorEl}
                anchorOrigin={{
                  vertical: 'top',
                  horizontal: 'right',
                }}
                transformOrigin={{
                  vertical: 'top',
                  horizontal: 'right',
                }}
                open={open}
                onClose={this.handleClose}
              >
                <MenuItem onClick={this.handleClose}>ATX Fabrication</MenuItem>
              </Menu>
            </Grid>
            <Grid item align="center" xs={4}>
              <img src={logo} className="App-logo" alt="logo" />
            </Grid>
            <Grid item align="end" xs={4}>
              <Button href="#about">
                About
              </Button>
              <Button href="#services">
                Services
              </Button>
              <Button href="#contact">
                Contact
              </Button>
            </Grid>
          </Grid>
        </header>
        <section>
          <div className="large" />
        </section>
        <section>

        </section>
      </div>
    )
  }
}

export default connect(null, null)(Header);



